import React from "react";
import logo from "./logo.PNG";
import Sect from "./illustration.png";
function Section1() {
  return (
    <div className="nav">
      <div className="logo-left">
        <img src={logo} />
      </div>
      <div className="logo-right">
        <div className="right-div">
          <select id="select">
            <option>Why Marvel</option>
          </select>
          <p id="signup">Sign up free</p>
          <p>Sign in</p>
          <p>Pricing</p>
          <p>Enterprise</p>
        </div>
      </div>

      <div className="section-two">
        <div className="section-two-left">
          <h1>
            Let's build the future <br /> of design, now.
          </h1>
          <p>
            Create amazing tools and integrations for 1 million <br />
            users or customise Marvel for your team
          </p>
        </div>

        <div className="section-two-right">
          <img src={Sect} />
        </div>

        <div className="button">
          <p id="button">Submit your app</p>
        </div>
        <div className="button1">
          <p id="button1">View API Docs</p>
        </div>
      </div>
      <div className="Section-three">
        <div className="section-three-left">
          <h1>Experience the freedom to build features, right away.</h1>
          <p>
            You can now harness Component of our platform and build powerful
            integration for our 2 million users or simply just for your teams.
          </p>
           </div>
                </div>
      <div className="Section-four">
     
          <div className="section-four-left">
            <h1>Dedicated API Team</h1>
            <p>
              Our team are available for user's questions via <br />
              our Slack Developer Community and Email
            </p>
          </div>
        </div>
    </div>
  );
}
export default Section1;
